'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/auth-context';
import { useLanguage } from '@/contexts/language-context';
import { Navbar } from '@/components/navbar';
import { motion } from 'framer-motion';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { ArrowLeft } from 'lucide-react';

export default function AdminSignsPage() {
  const { user, isAdmin, loading } = useAuth();
  const { t } = useLanguage();
  const router = useRouter();

  useEffect(() => {
    if (!loading && (!user || !isAdmin)) {
      router.push('/');
    }
  }, [user, isAdmin, loading, router]);

  if (loading || !user || !isAdmin) {
    return null;
  }

  return (
    <div className="min-h-screen bg-background text-foreground">
      <Navbar />

      <div className="container mx-auto px-6 py-8 max-w-7xl pt-28">
        <Button variant="ghost" asChild className="mb-6 pl-0 hover:bg-transparent hover:text-primary">
          <Link href="/admin" className="flex items-center gap-2">
            <ArrowLeft className="w-4 h-4" />
            {t('admin.backToDashboard')}
          </Link>
        </Button>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <div className="mb-8">
            <h1 className="text-3xl font-bold mb-2">{t('admin.trafficSigns')}</h1>
            <p className="text-muted-foreground">
              {t('admin.trafficSignsDesc')}
            </p>
          </div>

          <div className="p-12 text-center border border-dashed border-border rounded-xl bg-card/50">
            <p className="text-muted-foreground">
              Traffic Signs Management Interface Coming Soon.
              <br />
              Please run the provided SQL script to seed the database first.
            </p>
          </div>
        </motion.div>
      </div>
    </div>
  );
}
